import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addPost } from "./postsSlice";

export const AddPostForm = () => {
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [userId, setUserId] = useState("");

  const users = useSelector((state) => state.users);

  const onTitleChanged = (e) => setTitle(e.target.value);
  const onContentChanged = (e) => setContent(e.target.value);
  const onAuthorChanged = (e) => setUserId(e.target.value);

  const dispatch = useDispatch();

  return (
    <section>
      <h2>Add a New Post</h2>
      <form>
        <label>
          Author:
          <select onChange={onAuthorChanged}>
            <option value=""></option>
            {users.map((user) => (
              <option key={user.id} value={user.id}>
                {user.name}
              </option>
            ))}
          </select>
        </label>
        <label>
          <p style={{ marginBottom: 0 }}>Post Title:</p>
          <input placeholder="" value={title} onChange={onTitleChanged} />
        </label>
        <label>
          Content:
          <textarea
            placeholder=""
            value={content}
            onChange={onContentChanged}
          />
        </label>
        <button
          disabled={!(title && content && userId)}
          type="button"
          onClick={() => {
            if (title && content) dispatch(addPost(title, content, userId));
            setTitle("");
            setContent("");
          }}
        >
          Save Post{" "}
        </button>
      </form>
    </section>
  );
};
