import { createSlice, nanoid } from "@reduxjs/toolkit";
import { sub } from "date-fns";

const initialState = [
  {
    id: "0",
    title: "Post 1", // omitted fields
    content: "Hello!",
    date: sub(new Date(), { minutes: 10 }).toISOString(),
    user: "0",
    reactions: { thumbsUp: 1, hooray: 0, heart: 0, rocket: 0, eyes: 0 }
  },
  {
    id: "1",
    title: "Post 2",
    content: "More text",
    date: sub(new Date(), { minutes: 5 }).toISOString(),
    user: "1",
    reactions: { thumbsUp: 3, hooray: 0, heart: 0, rocket: 0, eyes: 0 }
  }
];

const postsSlice = createSlice({
  name: "posts",
  initialState,
  reducers: {
    addPost: {
      reducer(state, action) {
        state.push(action.payload);
      },
      prepare(title, content, userId) {
        return {
          payload: {
            id: nanoid(),
            date: new Date().toISOString(),
            title,
            content,
            user: userId,
            reactions: { thumbsUp: 0, hooray: 0, heart: 0, rocket: 0, eyes: 0 }
          }
        };
      }
    },
    reactionAdded(state, action) {
      const { postId, reaction } = action.payload;
      const existingPost = state.find((post) => post.id === postId);
      if (existingPost) {
        existingPost.reactions[reaction]++;
      }
    },
    updatePost(state, action) {
      let index = state.findIndex((post) => post.id === action.payload.id);
      state[index] = action.payload;
    }
  }
});

export const { addPost, updatePost, reactionAdded } = postsSlice.actions;

export default postsSlice.reducer;
